nav {
  display: flex;
  padding: 0.3vw 7vw;
  justify-content: space-between;

  background: rgba(255, 255, 255, 0.6);
  box-shadow: 0rem 0.375rem 2.25rem 0.25rem rgba(0, 0, 0, 0.06);
  backdrop-filter: blur(1.563rem);

  white-space: nowrap;
}

nav .general-options {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

nav .nav-link {
  color: var(--Black-500, #667176);
  text-align: center;
  text-decoration: none;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5rem;
}

nav .general-options>a.active {
  font-weight: 31.25rem;
  color: var(--Blue-400, #009ad6);
}

/* language options */
nav .language-options {
  display: flex;
  padding: 0.25rem;
  align-items: flex-start;
  border-radius: 0.563rem;
  background: var(--Blue-25, #e6f8ff);
}

#togglBtn-1 #togglBtn-2 {
  display: grid;
  padding: 0.375rem 0.875rem;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5rem;
  border-radius: 0.375rem;
  border: none;
}

nav .language-link {
  display: grid;
  padding: 0.375rem 0.875rem;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5rem;
  border-radius: 0.375rem;
  border: none;
}

.language-link:hover {
  background-color: transparent;
}

nav .lang {
  text-align: center;
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 1.25rem;
}

.line {
